import BlogHeader from "@/components/BlogHeader";
import Footer from "@/components/footer";
import { sanityClient } from "@/lib/sanity";
import { PortableText } from "@portabletext/react";
import Link from "next/link";

export const dynamic = "force-dynamic";

/* ----------------------------------------
   HELPERS
---------------------------------------- */
function calculateReadTime(content = []) {
  const text = content
    .filter((block) => block._type === "block")
    .flatMap((block) => block.children?.map((child) => child.text) || [])
    .join(" ");

  const words = text.split(/\s+/).length;
  const minutes = Math.max(1, Math.ceil(words / 200));
  return `${minutes} min read`;
}

/* ----------------------------------------
   PORTABLE TEXT COMPONENTS
---------------------------------------- */
const portableTextComponents = {
  types: {
    code: ({ value }) => (
      <pre className="bg-gray-900 text-gray-100 rounded-xl p-5 overflow-x-auto my-8 text-sm">
        <code className="font-mono">{value.code}</code>
      </pre>
    ),

    image: ({ value }) => (
      <figure className="my-10">
        <img
          src={value.asset?.url}
          alt={value.alt || ""}
          className="rounded-2xl"
        />
        {value.caption && (
          <figcaption className="text-sm text-center text-gray-500 mt-2">
            {value.caption}
          </figcaption>
        )}
      </figure>
    ),
  },
};

/* ----------------------------------------
   PAGE
---------------------------------------- */
export default async function BlogPostPage({ params }) {
  const slug = params?.slug;

  if (!slug) {
    return null;
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      _id,
      title,
      excerpt,
      publishedAt,
      mainImage{ asset->{url} },
      categories[]->{_id,title},
      author->{ name, image{asset->{url}} },
      content
    }`,
    { slug }
  );

  if (!post) {
    return (
      <>
        <BlogHeader />
        <main className="max-w-3xl mx-auto px-5 py-24 text-center">
          <h1 className="text-3xl font-bold">Post not found</h1>
          <Link href="/blog" className="text-blue-600 mt-4 inline-block">
            ‚Üê Back to blog
          </Link>
        </main>
        <Footer />
      </>
    );
  }

  const readTime = calculateReadTime(post.content);

  return (
    <>
      <BlogHeader />

      <main className="max-w-3xl mx-auto px-5 py-20">
        {/* Categories */}
        <div className="flex flex-wrap gap-2 mb-5">
          {post.categories?.map((cat) => (
            <span
              key={cat._id}
              className="text-xs font-medium bg-blue-50 text-blue-600 px-3 py-1 rounded-full"
            >
              {cat.title}
            </span>
          ))}
        </div>

        {/* Title */}
        <h1 className="text-4xl md:text-5xl font-bold tracking-tight text-gray-900 mb-6">
          {post.title}
        </h1>

        {/* Meta */}
        <div className="flex flex-wrap items-center gap-4 text-sm text-gray-500 mb-10">
          {post.author?.image?.asset?.url && (
            <img
              src={post.author.image.asset.url}
              alt={post.author.name}
              className="w-9 h-9 rounded-full object-cover"
            />
          )}
          <span className="font-medium text-gray-700">
            {post.author?.name}
          </span>
          <span>
            {new Date(post.publishedAt).toLocaleDateString("en-IN", {
              day: "numeric",
              month: "long",
              year: "numeric",
            })}
          </span>
          <span>{readTime}</span>
        </div>

        {/* Featured Image */}
        {post.mainImage?.asset?.url && (
          <img
            src={post.mainImage.asset.url}
            alt={post.title}
            className="w-full max-h-[420px] object-cover rounded-3xl mb-14"
          />
        )}

        {/* Content */}
        <article
          className="
            prose 
            prose-lg 
            prose-slate 
            max-w-none

            prose-headings:font-bold
            prose-headings:tracking-tight
            prose-headings:text-gray-900

            prose-p:text-gray-700
            prose-p:leading-8

            prose-a:text-blue-600
            prose-a:no-underline
            hover:prose-a:underline

            prose-strong:text-gray-900

            prose-blockquote:border-l-blue-600
            prose-blockquote:text-gray-700

            prose-ul:pl-6
            prose-ol:pl-6
          "
        >
          <PortableText
            value={post.content}
            components={portableTextComponents}
          />
        </article>
      </main>

      <Footer />
    </>
  );
}
